.Auth-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-image: url(/static/media/fernLanding.46421756.png);
  background-repeat: no-repeat;
  background-size: cover;
}

#login.Auth-form {
  background-color: #364958;
}

#signup.Auth-form {
  background-color: #364958;
}

.Auth-form {
  width: 420px;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  padding-top: 30px;
  padding-bottom: 20px;
  border-radius: 8px;
  background-color: white;
  z-index: 1;
}

.Auth-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Auth-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
}

label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}

input {
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: 1px solid rgb(204, 204, 204);
  padding-left: 12px;
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}

input:focus {
  border: 1px solid rgb(0, 0, 0);
}

.Auth-form-button {
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: 1px solid rgb(204, 204, 204);
  padding-left: 12px;
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
  background-color: rgb(0, 0, 0);
  cursor: pointer;
}

.Auth-form-button:hover {
  background-color: rgb(34, 34, 34);
  color: rgb(255, 255, 255);
}

.Auth-form-button:focus {
  background-color: rgb(34, 34, 34);
  color: rgb(255, 255, 255);
}

.Auth-form-button:active {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button:disabled {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button:disabled:hover {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button:disabled:focus {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button:disabled:active {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
/* LOGIN AND LOGOUT END */


