@import './Styles/AccountBalances.css';
@import './Styles/Trends.css';
@import './Styles/LinkAccount.css';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing{
  background-image: 'fernLanding.jpg';
}

/* Contact */
.bakers {
  font-size: 30px;
  text-align: center;
  margin-bottom: 0px;
}

.members {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

/* accounts overview component */
.account-title-style {
  background: "green"
}

.contact-desc {
  text-align: center;
  margin-top: 0px;
  margin-bottom: 0.5px;
  padding: 0px;
}

.contact-header {
  margin: 2rem;
  font-size: 40px;
  text-align: center;
}

.cover-container {
  height: 100vh;
  background-image: url(https://images.unsplash.com/photo-1576175789566-c808a91c8b51?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=735&q=80);
  background-size: cover;
  background-position: center;
}

.cover-container p {
  z-index: 1;
  font-size: 2.5rem;
  font-style: normal;
  color: white;
  line-height: 1.5em;
  text-align: center;
}

.home-main {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

/* Transactions Table Pagination */
.paginationBtns a {
  height: 40px;
  list-style: none;
  text-decoration: none;
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid black;
  color: black;
  cursor: pointer;
}

.paginationBtns {
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
}

.paginationBtns a:hover {
  background-color: rgb(7, 176, 7)
}

.paginationActive a {
  background-color: rgb(7, 176, 7)
}

/* LOGIN AND LOGOUT START */
.Auth-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-image: url('fernLanding.png');
  background-repeat: no-repeat;
  background-size: cover;
}

@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.9.1/font/bootstrap-icons.css");

#login.Auth-form {
  background-color: #364958;
}

#signup.Auth-form {
  background-color: #364958;
}

.Auth-form {
  width: 420px;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  padding-top: 30px;
  padding-bottom: 20px;
  border-radius: 8px;
  background-color: white;
  z-index: 1;
}

.Auth-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Auth-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
}

label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}

input {
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: 1px solid rgb(204, 204, 204);
  padding-left: 12px;
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}

input:focus {
  border: 1px solid rgb(0, 0, 0);
}

.Auth-form-button {
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: 1px solid rgb(204, 204, 204);
  padding-left: 12px;
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
  background-color: rgb(0, 0, 0);
  cursor: pointer;
}

.Auth-form-button:hover {
  background-color: rgb(34, 34, 34);
  color: rgb(255, 255, 255);
}

.Auth-form-button:focus {
  background-color: rgb(34, 34, 34);
  color: rgb(255, 255, 255);
}

.Auth-form-button:active {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button:disabled {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button:disabled:hover {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button:disabled:focus {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button:disabled:active {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

.Auth-form-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
/* LOGIN AND LOGOUT END */


