.text-blur-out {
  width: 100vw;
  height: 100vh;
  z-index: 2;
  /* background-size: 100% 100%; */
  /* background-repeat: no-repeat; */
	animation: text-blur-out 5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

@keyframes text-blur-out {
  0% {
    filter: blur(0.01);
  }
  100% {
    filter: blur(12px) opacity(0%);
  }
}

