.spinner {
  margin: 24px auto;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  border: 6px solid gray;
  border-top: 6px solid gray;
  animation: rotating 1.2s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

button {
  border: 1px solid #364958;
  border-radius: 5px;
  background: #364958;
  color: white;
  font-size: 18px;
  margin-right: 2cm;
}

@keyframes rotating {
  100% {
    transform: rotate(360deg);
  }
}
